*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Chakra Petch", sans-serif;
  font-size: 16px;
  line-height: 24px;
  display: grid;
  grid-template-rows: min-content auto min-content;
  grid-template-columns: 100%;
  min-width: 360px;
  max-width: 100%;
  width: 100%;
  min-height: 100vh;
  background-color: #171717;
  color: #FFFFDF;
  overflow-x: hidden;
}
@media (width >= 1440px) {
  body {
    grid-template-rows: auto min-content;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

.page-body.active {
  position: relative;
  overflow: hidden;
}
.page-body.active .page-main::before {
  position: absolute;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  content: "";
  background: rgba(23, 23, 23, 0.5);
}
.page-body.page-home .page-main {
  padding-top: 0;
}
@media (width >= 720px) {
  .page-body.page-home section > .container {
    max-width: 1156px;
    margin: 0 auto;
    padding: 0 24px;
  }
}
.page-body.page-marketplace::after {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 90%;
  z-index: -1;
  content: "";
  background: radial-gradient(50% 50% at 50% 50%, rgba(23, 23, 23, 0.1) 0%, #171717 100%), url("~images/bg.jpg") lightgray 50%/cover no-repeat;
}
.page-body.page-register {
  grid-template-rows: min-content auto;
  background: #1F1F1F;
}

.page-main {
  padding-top: 60px;
}
@media (width >= 768px) {
  .page-main {
    padding-top: 80px;
  }
}

.container {
  margin: 0 16px;
}
@media (width >= 768px) {
  .container {
    margin: 0 24px;
  }
}
@media (width >= 1440px) {
  .container {
    max-width: 1380px;
    margin: 0 auto;
  }
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: transparent;
  white-space: nowrap;
  font-variant-numeric: ordinal;
  font-feature-settings: "ss01" on, "salt" on, "ss08" on, "ss06" on, "ss03" on, "ss05" on, "ss04" on, "ss09" on, "clig" off, "liga" off;
  font-style: normal;
  line-height: normal;
  cursor: pointer;
}
.button--yellow {
  gap: 8px;
  padding: 9px 29px;
  color: #FFFFDF;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  font-family: "Chakra Petch", sans-serif;
  border-radius: 14px;
  border: 1px solid #FFB932;
  background: linear-gradient(180deg, rgba(255, 185, 0, 0.5) 21.8%, rgba(255, 144, 0, 0.5) 120.8%);
  box-shadow: 0px 0px 20px 0px #FFB932 inset;
  backdrop-filter: blur(20px);
  transition: background 0.2s ease-out, box-shadow 0.2s ease-out;
}
.button--yellow:hover {
  background: #171717;
  box-shadow: 0px 0px 32px 0px rgba(255, 210, 50, 0.5) inset;
}
.button--grey {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  padding: 4px 4px 4px 16px;
  color: #FFFFDF;
  font-family: "Chakra Petch", sans-serif;
  font-size: 14px;
  font-weight: 400;
  border-radius: 16px;
  background: #3D3D3D;
  backdrop-filter: blur(20px);
  transition: background 0.2s ease-out;
}
.button--grey:hover, .button--grey.active {
  background: radial-gradient(50.07% 75.18% at 50% 54.51%, #FFB900 0%, #FF9000 100%);
}
.button--black {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 40px;
  font-family: "Chakra Petch", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  color: #FFB932;
  border-radius: 14px;
  background: #323232;
  box-shadow: 0px 4px 18px 0px rgba(244, 150, 2, 0.3);
  transition: background 0.2s ease-out, box-shadow 0.2s ease-out;
}
@media (width >= 720px) {
  .button--black {
    padding-right: 14px;
  }
}
.button--black:hover {
  background: #171717;
  box-shadow: 0px 4px 18px 0px rgba(244, 150, 2, 0.5);
}
.button--transparent {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 14.5px 17px;
  border-radius: 14px;
  border: 1px solid #FFFFDF;
  background: transparent;
  color: #FFFFDF;
  font-family: "Chakra Petch", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.button--transparent svg {
  fill: currentColor;
}
.button--flex {
  display: flex;
}

.hide-desktop {
  display: none;
}
@media (width >= 1440px) {
  .hide-desktop {
    display: block;
  }
}
@media (width >= 1440px) {
  .hide-mobile {
    display: none;
  }
}

.title {
  font-variant-numeric: ordinal;
  font-feature-settings: "ss01" on, "salt" on, "ss08" on, "ss06" on, "ss03" on, "ss05" on, "ss04" on, "ss09" on, "clig" off, "liga" off;
  font-family: "Cydekick", sans-serif;
  font-style: italic;
  font-weight: 400;
  line-height: 90%;
  color: #FFFFDF;
}
.title--2 {
  font-size: 58px;
}
@media (width >= 720px) {
  .title--2 {
    font-size: 112px;
  }
}
.title--3 {
  font-size: 32px;
}
@media (width >= 720px) {
  .title--3 {
    font-size: 40px;
  }
}
.title--4 {
  color: #FFB932;
  font-family: "Quantico", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  font-style: normal;
}
@media (width >= 720px) {
  .title--4 {
    font-size: 32px;
  }
}
.title--5 {
  color: #FFB932;
  font-family: "Quantico", sans-serif;
  font-style: normal;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
}

.paragraph {
